
import { defineComponent } from "vue";
import CaseCanvas from "@/components/CaseCanvas.vue";
import {
	signInWithPopup,
	getAuth,
	GoogleAuthProvider,
	signOut,
} from "firebase/auth";
import CasePopup from "@/components/CasePopup.vue";

export default defineComponent({
	name: "HomeView",
	data() {
		return {
			authed: false,
			admin: false,
			inventory: [] as any,
			showPopup: false,
			selectedItem: {},
			loaded: false,
		};
	},
	methods: {
		closePopup() {
			this.getInventory();
			this.showPopup = false;
		},
		async loginUserWithBackend() {
			const user = getAuth().currentUser;
			if (user) {
				this.authed = true;
				const idToken = await user.getIdToken();
				const res = await fetch("/api/login", {
					method: "POST",
					headers: {
						Accept: "application/json",
						"Content-Type": "application/json",
						"CSRF-Token": this.$cookies.get("XSRF-TOKEN"),
					},
					body: JSON.stringify({
						idToken,
					}),
				});

				if (res.ok) {
					const adminAccess = await res.json();
					this.admin = adminAccess;
					this.getInventory().then(() => {
						this.loaded = true;
					});
				} else if (res.status === 401) {
					this.authed = false;
					this.admin = false;
					this.loaded = false;
				}
			} else {
				this.authed = false;
				this.admin = false;
				this.loaded = false;
			}
		},
		googleSignIn() {
			signInWithPopup(getAuth(), new GoogleAuthProvider())
				.then((data) => {
					this.loginUserWithBackend();
				})
				.catch((err) => {
					console.log("Error Signing in with Google " + err);
				});
		},
		signOutUser() {
			signOut(getAuth()).then(() => {
				this.authed = false;
				this.admin = false;
				this.loaded = false;
			});
		},
		async getInventory() {
			let idToken = (await getAuth().currentUser?.getIdToken()) || "";
			fetch(
				"/api/inventory?" +
					new URLSearchParams({
						idToken,
					})
			)
				.then(async (res) => {
					if (res.ok) {
						this.inventory = await res.json();
						console.log(this.inventory);
						(
							this.$refs.inventoryItem as unknown as any
						).updateValues();
					} else {
						console.log(res);
					}
				})
				.catch((err) => {
					console.log(err);
				});
		},
		selectCase(item: any) {
			this.showPopup = true;
			this.selectedItem = item;
			console.log(this.selectedItem);
		},
	},
	mounted() {
		fetch("/api/csrf");

		this.loginUserWithBackend();
	},
	components: { CaseCanvas, CasePopup },
});
